@import url(https://fonts.googleapis.com/css2?family=Barlow:wght@100;200;400;500;800&display=swap);
body{margin:0;font-family:'Barlow', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}*{margin:0;padding:0;outline:0;text-decoration:none}

.header{width:100%;height:80px;display:flex;align-items:center}.header .navbar{display:flex;justify-content:space-between;width:100%;padding:0 24px}.header .navbar div{display:flex;align-items:center}.header .navbar div a{margin-left:32px;color:#000;font-weight:500;font-size:16px}.header .navbar div .search{height:40px;width:240px;border:1px solid #F2F2F2;border-radius:24px}.header .navbar div .search input{border:none;height:100%;padding:0 16px;border-radius:24px}.header .navbar div .search input::-webkit-input-placeholder{color:#EE0E7D;font-weight:bold}.header .navbar div .search input:-ms-input-placeholder{color:#EE0E7D;font-weight:bold}.header .navbar div .search input::placeholder{color:#EE0E7D;font-weight:bold}.header .navbar div .search img{cursor:pointer}

.banner{width:100%;height:560px;background:#03FFA5;display:flex;align-items:center;justify-content:center}.banner .text-banner{display:flex;flex-direction:column;align-items:flex-start}.banner .text-banner h1{width:451px;font-size:64px;font-weight:500;line-height:77px;text-transform:uppercase}.banner .text-banner p{width:329px;height:72px;padding:16px 0 35px;font-weight:500;font-size:16px;line-height:24px}.banner .text-banner a{height:24px;width:188px;font-size:16px;color:#000;font-weight:500;padding:10px;border:1px solid #000;border-radius:8px;text-align:center}.banner .product-banner{display:flex;width:461px;height:480px;align-items:center;justify-content:center}

.marcas{width:100%;height:80px;background:#F2F2F2;display:flex;align-items:center;justify-content:center}.marcas .marcas-bar{width:70%;display:flex;align-items:center;justify-content:space-around}

.solutions{width:100%;height:557px;display:flex;flex-direction:column;align-items:center}.solutions h1{width:80%;height:40px;padding-top:56px;font-size:32px;font-weight:700;text-transform:uppercase}.solutions .solutions-list{display:flex;width:80%;align-items:center;margin-top:24px;justify-content:space-between}.solutions .solutions-list .solutions-card{width:241px;height:381px;border:1px solid #F2F2F2;border-radius:4px;padding:16px}.solutions .solutions-list .solutions-card .card-header{width:100%;margin-bottom:53px}.solutions .solutions-list .solutions-card .card-header .card-image{width:100px;height:100px;background:#F2F2F2;border-radius:50%;font-size:32px;font-weight:500;display:flex;align-items:center;justify-content:center}.solutions .solutions-list .solutions-card .card-content{width:100%}.solutions .solutions-list .solutions-card .card-content h3{margin-bottom:16px;font-size:16px;font-weight:500}.solutions .solutions-list .solutions-card .card-content h4{margin-bottom:16px;font-weight:500;font-size:12px;color:#EE0E7D}.solutions .solutions-list .solutions-card ul{margin-left:20px}.solutions .solutions-list .solutions-card ul li{color:#5B5B5B;font-size:12px;line-height:18px;margin-bottom:16px}.solutions .solutions-list .solutions-card .card-footer button{width:100%;height:48px;background:#03FFA5;margin:16px 0px;border:none;border-radius:6px;font-weight:500;font-size:16px;cursor:pointer}

.quemsomos{width:100%;height:648px;display:flex;align-items:center;justify-content:center}.quemsomos .conteudo-quemsomos{display:flex;align-items:center;justify-content:center;flex-direction:column}.quemsomos .conteudo-quemsomos h1{width:241px;height:80px;font-weight:bold;font-size:32px;line-height:40px;text-transform:uppercase}.quemsomos .conteudo-quemsomos p{width:241px;height:292px;margin-top:24px;font-weight:500;font-size:16px;line-height:24px;margin-bottom:36px}.quemsomos .conteudo-quemsomos a{height:24px;width:188px;font-size:16px;color:#000;font-weight:500;padding:10px;border:1px solid #000;border-radius:8px;text-align:center;margin-right:auto}.quemsomos .foto-quemsomos{width:676px;height:520px;margin:64px}

.contato{width:100%;height:480px;background:#EE0E7D;display:flex;align-items:center;justify-content:center;flex-direction:column}.contato p{max-width:686px;color:#fff;font-weight:bold;font-size:32px;line-height:40px;text-align:center;text-transform:uppercase}.contato .for-contato{font-weight:500;font-size:16px;line-height:24px;padding-top:32px;text-transform:inherit}.contato .contato-email{height:24px;font-weight:500;font-size:32px;line-height:24px;padding-top:8px;text-transform:inherit}

.newsletter{width:100%;height:436px;background:#000;display:flex;align-items:center;justify-content:center;flex-direction:column}.newsletter p{max-width:580px;height:80px;color:#fff;font-weight:bold;font-size:32px;line-height:40px;text-align:center;text-transform:uppercase}.newsletter .email-register{max-width:100%;border-bottom:0.5px solid #03FFA5;align-items:center;justify-content:center;display:flex}.newsletter .email-register input{background:#000;border:none;padding-top:34px;height:60px;width:320px}.newsletter .email-register input::-webkit-input-placeholder{font-weight:500;font-size:16px;line-height:24px}.newsletter .email-register input:-ms-input-placeholder{font-weight:500;font-size:16px;line-height:24px}.newsletter .email-register input::placeholder{font-weight:500;font-size:16px;line-height:24px}.newsletter .email-register button{max-width:89px;background:#000;color:#03FFA5;border:none;cursor:pointer;font-weight:bold;font-size:15px;line-height:40px;text-transform:uppercase;padding-top:34px}

.footer{width:100%;height:80px;display:flex;align-items:center;background:#000}.footer .footer-marcas{display:flex;justify-content:space-around;width:100%}.footer .footer-marcas .social-networks img{padding-right:17px}

.heroes{width:100%;display:flex;flex-direction:column;align-items:center;height:100%}.heroes h1{width:80%;height:40px;padding-top:56px;font-size:32px;font-weight:700;text-transform:uppercase}.heroes .heroes-list{justify-content:space-between;display:flex;width:80%;margin-top:24px;flex-wrap:wrap}.heroes .heroes-list .heroes-card{margin-right:10px;width:220px;border:1px solid #000;border-radius:4px;padding:16px;margin-bottom:25px}.heroes .heroes-list .heroes-card .card-header{width:100%;margin-bottom:53px}.heroes .heroes-list .heroes-card .card-header .card-image{width:100px;height:100px;background:#F2F2F2;border-radius:50%;font-size:32px;font-weight:500;display:flex;align-items:center;justify-content:center}.heroes .heroes-list .heroes-card .card-content{width:100%}.heroes .heroes-list .heroes-card .card-content h3{margin-bottom:16px;font-size:16px;font-weight:500}.heroes .heroes-list .heroes-card .card-content h4{margin-bottom:16px;font-weight:500;font-size:12px;color:#EE0E7D}.heroes .heroes-list .heroes-card ul{margin-left:20px}.heroes .heroes-list .heroes-card ul li{color:#5B5B5B;font-size:12px;line-height:18px;margin-bottom:16px}.heroes .heroes-list .heroes-card .card-footer button{width:100%;height:48px;background:#03FFA5;margin:16px 0px;border:none;border-radius:6px;font-weight:500;font-size:16px;cursor:pointer}

