.banner {
    width: 100%;
    height: 560px;
    background: #03FFA5;
    display: flex;
    align-items: center;
    justify-content: center;
  
    .text-banner {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      h1 {
        width: 451px;
        font-size: 64px;
        font-weight: 500;
        line-height: 77px;
        text-transform: uppercase;
      }
  
      p {
        width: 329px;
        height: 72px;
        padding: 16px 0 35px;
        font-weight: 500;
        font-size: 16px;
        line-height: 24px;
      }
  
      a {
        height: 24px;
        width: 188px;
        font-size: 16px;
        color: #000;
        font-weight: 500;
        padding: 10px;
        border: 1px solid #000;
        border-radius: 8px;
        text-align: center;
      }
    }
    .product-banner {
      display: flex;
      width: 461px;
      height: 480px;
      align-items: center;
      justify-content: center;
    }
  }